<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-grid">
                <div class="p-col-fixed">
                    <div class="p-formgroup-inline">
                        <div class="p-field-checkbox">
                            <p-checkbox
                                v-model="form.upToVoid"
                                :binary="true"
                            />
                            <label for="city3">Up to Void</label>
                        </div>
                        <div class="p-field-checkbox">
                            <p-checkbox
                                v-model="form.fluidsPo"
                                :binary="true"
                            />
                            <label for="city4">Fluids PO</label>
                        </div>
                        <div class="p-field-checkbox">
                            <p-checkbox v-model="form.cramps" :binary="true" />
                            <label for="city4">Cramps</label>
                        </div>
                        <div class="p-field-checkbox">
                            <p-checkbox
                                v-model="form.bleeding"
                                :binary="true"
                            />
                            <label for="city4">Bleeding Checked</label>
                        </div>
                        <div class="p-field-checkbox">
                            <p-checkbox v-model="form.nausea" :binary="true" />
                            <label for="city4">Nausea/Vomiting</label>
                        </div>
                    </div>
                </div>

                <div class="p-col-12 p-md-2">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Is Disconnect
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                v-model="form.ivDisconnect"
                                id="by"
                                :options="options"
                                optionLabel="label"
                            />
                        </div>
                    </div>
                </div>
            </div>
            <div class="p-fluid p-grid">
                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Amount of Fluid Given
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.fluidGiven" />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed">
                    <div class="p-formgroup-inline">
                        <div class="p-field-checkbox">
                            <p-checkbox
                                v-model="form.discharged"
                                :binary="true"
                            />
                            <label>Discharged home with instructions</label>
                        </div>
                        <div class="p-field-checkbox">
                            <p-checkbox
                                v-model="form.accompanied"
                                :binary="true"
                            />
                            <label>Accompanied</label>
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            RN/RPN
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.rnRpn" />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            MD
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.md" />
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { computed, inject, reactive, watch } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();
        const bottonForm = computed(() => store.getters["opu/getBottomForm"]);
        const form = reactive({
            upToVoid: false,
            fluidsPo: false,
            cramps: false,
            bleeding: false,
            nausea: false,
            discharged: false,
            accompanied: false,
            rnRpn: null,
            md: null,
            ivDisconnect: null,
            fluidGiven: null
        });

        watch(bottonForm, (newValue) => {
            form.upToVoid = newValue.upToVoid;
            form.fluidsPo = newValue.fluidsPo;
            form.cramps = newValue.cramps;
            form.bleeding = newValue.bleeding;
            form.nausea = newValue.nausea;
            form.discharged = newValue.discharged;
            form.accompanied = newValue.accompanied;
            form.rnRpn = newValue.rnRpn;
            form.md = newValue.md;
            form.ivDisconnect = newValue.ivDisconnect;
            form.fluidGiven = newValue.fluidGiven;
        });

        const opuForm = inject("opuForm");

        watch(form, (newValue) => {
            opuForm.upToVoid = newValue.upToVoid;
            opuForm.fluidsPo = newValue.fluidsPo;
            opuForm.cramps = newValue.cramps;
            opuForm.bleeding = newValue.bleeding;
            opuForm.nausea = newValue.nausea;
            opuForm.discharged = newValue.discharged;
            opuForm.accompanied = newValue.accompanied;
            opuForm.rnRpn = newValue.rnRpn;
            opuForm.md = newValue.md;
            opuForm.ivDisconnect = newValue.ivDisconnect;
            opuForm.fluidGiven = newValue.fluidGiven;
        });

        return { form, options: reactive([]) };
    }
};
</script>
