<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-grid">
                <div class="p-col-12 p-md-1">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            O2
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.o2" />
                        </div>
                        <label class="p-col-fixed p-mb-0">
                            L
                        </label>
                    </div>
                </div>

                <div class="p-col-12 p-md-1">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Via
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.o2via" />
                        </div>
                    </div>
                </div>

                <div class="p-col-12 p-md-8">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Comments
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.comment" />
                        </div>
                    </div>
                </div>

                <div class="p-col-12 p-md-2"></div>

                <div class="p-col-12 p-md-2">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Number of eggs retrived
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.eggsNum" />
                        </div>
                    </div>
                </div>

                <div class="p-col-12 p-md-2">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Return to recovery
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.recovery" />
                        </div>
                    </div>
                </div>

                <div class="p-col-12 p-md-1">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Bp
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.bpSummary" />
                        </div>
                    </div>
                </div>

                <div class="p-col-12 p-md-1">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Pulse
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.pulseSummary" />
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { computed, inject, reactive, watch } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();
        const middleForm = computed(() => store.getters["opu/getMiddleForm"]);
        const form = reactive({
            o2: null,
            o2via: null,
            comment: null,
            eggsNum: null,
            recovery: null,
            bpSummary: null,
            pulseSummary: null
        });

        watch(middleForm, (newValue) => {
            form.o2 = newValue.o2;
            form.o2via = newValue.o2via;
            form.comment = newValue.comment;
            form.eggsNum = newValue.eggsNum;
            form.recovery = newValue.recovery;
            form.bpSummary = newValue.bpSummary;
            form.pulseSummary = newValue.pulseSummary;
        });

        const opuForm = inject("opuForm");

        watch(form, (newValue) => {
            opuForm.o2 = newValue.o2;
            opuForm.o2via = newValue.o2via;
            opuForm.comment = newValue.comment;
            opuForm.eggsNum = newValue.eggsNum;
            opuForm.recovery = newValue.recovery;
            opuForm.bpSummary = newValue.bpSummary;
            opuForm.pulseSummary = newValue.pulseSummary;
        });

        return { form };
    }
};
</script>
